.currently_playing {
    background:coral;
}

.curved_border {
    border-radius:12px;
}

.curved_border_icon {
    border-radius:5px;
    height:auto;
    width:35%;
}

.sm_thumb_image, .vid_descript {
    display:inline-block;
}

.sm_thumb_image {
    width:30%;
    margin-right:10%;
}

.vid_descript {
    width:50%;
}

.embed {
    background:unset;
}

.item {
    border-radius:5px;
    box-shadow: 0 5px 20px #eee;
    margin:30px 0;
}